import React, { lazy, Suspense, Fragment } from 'react';
import { Switch, Redirect, Route } from 'react-router-dom';
import DashboardLayout from './layouts/DashboardLayout';
import LoadingScreen from './components/LoadingScreen';
import AuthGuard from './components/AuthGuard';
import GuestGuard from './components/GuestGuard';

const routesConfig = [
  {
    exact: true,
    path: '/404',
    component: lazy(() => import('./views/pages/Error404View'))
  },
  {
    exact: true,
    guard: GuestGuard,
    path: '/login',
    component: lazy(() => import('./views/auth/LoginView'))
  },
  {
    exact: true,
    path: '/login-unprotected',
    component: lazy(() => import('./views/auth/LoginView'))
  },
  {
    exact: true,
    guard: GuestGuard,
    path: '/register',
    component: lazy(() => import('./views/auth/RegisterView'))
  },
  {
    exact: true,
    path: '/register-unprotected',
    component: lazy(() => import('./views/auth/RegisterView'))
  },
  {
    path: '/',
    guard: AuthGuard,
    layout: DashboardLayout,
    routes: [
      {
        exact: true,
        path: '/',
        component: () => <Redirect to="/reports/dashboard" />
      },
      {
        exact: true,
        path: '/account',
        component: lazy(() => import('./views/pages/AccountView'))
      },
      {
        exact: true,
        path: '/reports/dashboard',
        component: lazy(() => import('./views/reports/DashboardView'))
      },
      {
        exact: true,
        path: '/reports',
        component: () => <Redirect to="/reports/dashboard" />
      },
      {
        exact: true,
        path: '/members',
        component: lazy(() => import('./views/member/MemberList/Member'))
      },
      {
        exact: true,
        path: '/members/create',
        component: lazy(() => import('./views/member/MemberCreate'))
      },
      {
        exact: true,
        path: '/members/:id/edit',
        component: lazy(() => import('./views/member/MemberEdit'))
      },
      {
        exact: true,
        path: '/members/:phone/chopDetail',
        component: lazy(() => import('./views/member/MemberChopDetail'))
      },
      {
        exact: true,
        path: '/members/:id/detail',
        component: lazy(() => import('./views/member/MemberDetail'))
      },
      {
        exact: true,
        path: '/ranks',
        component: lazy(() => import('./views/rank/RankList/Rank'))
      },
      {
        exact: true,
        path: '/ranks/create',
        component: lazy(() => import('./views/rank/RankCreate'))
      },
      {
        exact: true,
        path: '/ranks/:id/edit',
        component: lazy(() => import('./views/rank/RankEdit'))
      },
      {
        exact: true,
        path: '/branches',
        component: lazy(() => import('./views/branch/BranchList/Branch'))
      },
      {
        exact: true,
        path: '/branches/create',
        component: lazy(() => import('./views/branch/BranchCreate'))
      },
      {
        exact: true,
        path: '/branches/:id/edit',
        component: lazy(() => import('./views/branch/BranchEdit'))
      },
      {
        exact: true,
        path: '/consumeChopRules',
        component: lazy(() => import('./views/consumeChopRule/ConsumeChopRuleList/ConsumeChopRule'))
      },
      {
        exact: true,
        path: '/consumeChopRules/create',
        component: lazy(() => import('./views/consumeChopRule/ConsumeChopRuleCreate'))
      },
      {
        exact: true,
        path: '/consumeChopRules/:id/edit',
        component: lazy(() => import('./views/consumeChopRule/ConsumeChopRuleEdit'))
      },
      {
        exact: true,
        path: '/earnChopRules',
        component: lazy(() => import('./views/earnChopRule/EarnChopRuleList/EarnChopRule'))
      },
      {
        exact: true,
        path: '/earnChopRules/create',
        component: lazy(() => import('./views/earnChopRule/EarnChopRuleCreate'))
      },
      {
        exact: true,
        path: '/earnChopRules/:id/edit',
        component: lazy(() => import('./views/earnChopRule/EarnChopRuleEdit'))
      },
      {
        exact: true,
        path: '/chops/add',
        component: lazy(() => import('./views/chops/AddChops'))
      },
      {
        exact: true,
        path: '/chops/expired',
        component: lazy(() => import('./views/chops/ChopsExpiredSetting'))
      },
      {
        exact: true,
        path: '/users',
        component: lazy(() => import('./views/user/UserList/User'))
      },
      {
        exact: true,
        path: '/users/create',
        component: lazy(() => import('./views/user/UserCreate'))
      },
      {
        exact: true,
        path: '/users/:id/edit',
        component: lazy(() => import('./views/user/UserEdit'))
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  }
];

const renderRoutes = (routes) => {
  return routes ? (
    <Suspense fallback={<LoadingScreen />}>
      <Switch>
        {routes.map((route, i) => {
          const Guard = route.guard || Fragment;
          const Layout = route.layout || Fragment;
          const Component = route.component;

          return (
            <Route
              key={i}
              path={route.path}
              exact={route.exact}
              render={(props) => (
                <Guard>
                  <Layout>
                    {route.routes ? renderRoutes(route.routes) : <Component {...props} />}
                  </Layout>
                </Guard>
              )}
            />
          );
        })}
      </Switch>
    </Suspense>
  ) : null;
};

function Routes() {
  return renderRoutes(routesConfig);
}

export default Routes;
